import { useFetcher } from "react-router-dom"

export default function () {
    const fetcher = useFetcher()
    return <div>
        <fetcher.Form action="" method="post" className="grid gap-y-6 bg-amber-700 w-1/2 p-5 m-auto mt-9 rounded-xl">
            <input name="email" className="outline-none h-7 rounded-sm" type="email" placeholder='please enter your email address' aria-label="Email" required/>
            <textarea name="message" className="outline-none rounded-sm" placeholder='please enter your feedback' required/>
            <input className="bg-yellow-400 w-1/6 m-auto h-8 rounded-xl font-bold" type="submit" value='Send'/>
        </fetcher.Form>
    </div>
}