import { configureStore, createSlice } from "@reduxjs/toolkit";

const imageSlid = createSlice({
    name: 'image',
    initialState: { image: null },
    reducers: {
        StoreImage(state,action) {
            state.image = action.payload
        }
    }
})

export const { StoreImage } = imageSlid.actions

export default configureStore({
    reducer:{GetImage:imageSlid.reducer}
})