
import FollwerFrom from "../Component/FollwerFrom";
import UploadFiles from "../Upload/Image/upload";

export default function () {
    return <>
        <FollwerFrom/>
    </>
}

const store = async (desc, file) => {
    const api = await fetch('https://soland-9a89e-default-rtdb.firebaseio.com/posted.json', { method: 'POST',body:JSON.stringify({ desc: desc, url: file+'' }), headers: { 'Content-Type': 'application/json' } })
    if (api.ok) {
        const data = await api.json()
        console.log(data)
        alert("success")
    } else {
        alert("Something went wrong")
    }

}



const actions =async ({ request })=>{
    let data =await request.formData();
    const image = document.querySelector('#image');
    const file=image.files[0]
    UploadFiles(file).then((value) => {
        store(data.get('description'), value)
        //alert("successfully uploaded")
    })
    return null;
}
export { actions };