import userprofile from './IMG/userprifle.png'
import posted from './IMG/them4.png'

import AccessTimeIcon from '@mui/icons-material/AccessTime';
import CloseIcon from '@mui/icons-material/Close';
import { useState } from 'react';




export default function () {
    const [textChange, setTextChange] = useState(true);
    const [imageChange, setImageChange] = useState(false);
    const textwrap = () => {
        setTextChange(!textChange)
    }
    const imageHandler = () => { 
        setImageChange(!imageChange)
    }
    return <>

    <div className="md:w-1/2 sm:w-2/3  mx-auto mt-9 bg-teal-50 p-6 rounded-xl h-auto">
        <div className='flex items-center w-full'>
            <div className='w-14 h-14 rounded-full overflow-hidden cursor-pointer'>
                <img src={userprofile} className='w-14' />
            </div>
            <div className='flex flex-col ml-2'>
                <label className='cursor-pointer'>Tet Davann</label>
                <label className='text-xs'><AccessTimeIcon sx={{fontSize:12}}/>1d</label>
            </div> 
            <div className='ml-auto cursor-pointer'>
                <CloseIcon sx={{color:'red'}} />
            </div>
        </div>
        <div className={textChange ? 'truncate':''} onClick={textwrap}>
            <label>Lorem ipsum dolor sit amet, consectetur adip ipsum dolor sit amet, consectetur adip ipsum dolor sit amet, consectetur adip ipsum dolor sit amet, consectetur adip </label>
        </div>

        <div className='w-full mt-4 relative'>
            <img src={posted} className='h-60 w-full' alt='something went wrong!' onClick={imageHandler}/>
        </div>
        </div>
        {imageChange && <div className='w-full h-full transition-all' onClick={imageHandler}>
            <div className='bg-black opacity-50  fixed pt-20 w-full h-full top-0 left-0 transition-all'></div>
            <img src={posted} className=' m-auto w-2/3 fixed top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2' alt='something went wrong!'/>
        </div>}
        
    </>
}