import { NavLink } from "react-router-dom";

export default function() {
    return (
      <div className="bg-pink-700 p-3 flex lg:flex-row flex-col items-center">
        <div className="w-1/2 px-3">
          <form>
            <input className="w-full m-auto p-2 rounded-full pl-2 items-center outline-none h-full" type='search' placeholder="search" />
          </form>
        </div>
        <div className="container flex justify-end m-auto text-xl uppercase space-x-16 child-hover last:text-yellow-600 font-semibold">
          <NavLink to={''} className={({ isActive, isPending }) => isActive?"underline":isPending?"underline":""}>Home</NavLink>
          <NavLink to={'follower'} className={({ isActive, isPending }) => isActive ? 'underline' : isPending ? 'underline' : ''}>Follower</NavLink>
          <NavLink to={'community'} className={({ isActive,isPending}) => isActive ? 'underline' : isPending ? 'text-red-500' : ''}>Community</NavLink>
          <NavLink to={'blog'} className={({ isActive, isPending }) => isActive ? 'underline' : isPending ? 'underline' : ''}>Blog</NavLink>
          <NavLink to={'profile'} className={({ isActive, isPending }) => isActive ? 'underline' : isPending ? 'underline' : ''}>
            prifile
          </NavLink>
        </div>
      </div>
    )
  }