
import { useRef, useState } from "react"
import picture from './IMG/picture.png'
import {useFetcher } from "react-router-dom";


export default function () {
    const [image, setImage] = useState(picture);
    const imageRef = useRef()
    const descRef = useRef()
    const fetcher = useFetcher()

    const imageHandler = () => {
        const image = imageRef.current.files[0]
        const images = URL.createObjectURL(image)

        setImage(images)
    }


    return <fetcher.Form method="post" className="mt-7 text-center">
            <label htmlFor="image" className="w-full h-14">
                <img src={image} alt="Image" className="w-40 h-40 mx-auto"/>
        </label>
        <input ref={imageRef} onChange={imageHandler} className="hidden" type='file' accept=".png,.jpeg" name="image" id='image' placeholder="please choose a photo" required />           
        <br />
        <div className="w-full grid grid-cols-5">
            <textarea ref={descRef} name="description" className="h-28 outline-none col-start-2 col-span-3"></textarea>
        </div>
        <br/>
        <button type="submit" className="w-32 h-7 font-bold bg-yellow-500 text-center rounded-2xl">Post</button>
    </fetcher.Form>
}